.about {
  color: #fff;
  height: 100vh;
  display: flex;
  align-items: center;
  &__background {
    position: fixed;
    top: 0;
    z-index: 10;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background: #323232;
    opacity: 0.5;
    position: absolute;
    right: 0;
    z-index: 12;
  }
  &__name {
    z-index: 100;
    display: flex;
    margin: 0;
  }
}